<template>
<div  >
    <v-row>
    <v-col cols="12" >
    <section id="contactform">
        <v-card
        elevation="2"
        >
        <v-card-title v-if="!respMessage">Send notification for users</v-card-title>
        <v-card-title v-else >Message sent successfully</v-card-title>
            <v-form class="pa-6" ref="Form" v-model="valid" lazy-validation>
                <v-select
                :items="items_type"
                item-text="text"
                item-value="id"
                v-model="type"
                label="User Type"
                type="number"
                ></v-select>
                <v-select
                :items="items_verified"
                item-text="text"
                item-value="id"
                v-model="verified"
                label="E-mail verified"
                ></v-select>
                <v-select
                :items="items_status"
                :error-messages="statusErrors"
                item-text="text"
                item-value="id"
                v-model="status"
                label="User Status"
                required
                @input="$v.status.$touch()"
                @blur="$v.status.$touch()"
                ></v-select>
                <v-text-field
                v-model="title"
                :error-messages="titleErrors"
                :counter="45"
                label="Title"
                required
                @input="$v.title.$touch()"
                @blur="$v.title.$touch()"
                ></v-text-field>
                <v-text-field
                v-model="greeting"
                :error-messages="greetingErrors"
                :counter="45"
                label="Greeting"
                required
                @input="$v.greeting.$touch()"
                @blur="$v.greeting.$touch()"
                ></v-text-field>

                <v-textarea
                rows="2"
                v-model="body"
                label="Body"
                :counter="50000"
                :error-messages="bodyErrors"
                @change="$v.body.$touch()"
                @blur="$v.body.$touch()"
                required
                ></v-textarea>
                <v-textarea
                rows="2"
                v-model="html_body"
                label="HTML body"
                :counter="5000000"
                ></v-textarea>

                <v-text-field
                v-model="thanks"
                :error-messages="greetingErrors"
                :counter="45"
                label="Thanks"
                required
                @input="$v.greeting.$touch()"
                @blur="$v.greeting.$touch()"
                ></v-text-field>
                <v-text-field
                v-model="url"
                :counter="191"
                label="Url (route)"
                ></v-text-field>


            <v-card-actions>
                <v-btn
                outlined
                class="mr-4"
                @click="submit"
                :disabled="!valid"
                :loading="loading"
                >
                submit
                </v-btn>
                <v-btn
                outlined
                @click="clear"
                color="indigo"
                v-if="!loading"
                >
                clear
                </v-btn>

            </v-card-actions>

            </v-form>
            </v-card>

    </section>
    </v-col>

    </v-row>
</div>
</template>
<script>
  import { validationMixin } from 'vuelidate'
  import { required, numeric, maxLength } from 'vuelidate/lib/validators'

  export default {
    mixins: [validationMixin],

    validations: {
      greeting: { required, maxLength: maxLength(45) },
      thanks: { required, maxLength: maxLength(45) },
      title: { required, maxLength: maxLength(45) },
      body: { required,},
      status: { required,},
      type: { numeric, },
    },

    data: () => ({
        respMessage:false,
        valid: true,
        loading: false,
        items_verified:[
            {text: 'Yes', id: true},
            {text: 'No', id: false},
        ],
        items_type:[
            {text: 'Admin', id:'1'},
            {text: 'Client', id:'2'},
            {text: 'Chef', id:'3'}
        ],
        items_status:[
            {text: 'Activated', id:'1'},
            {text: 'Deactivated', id:'0'},
        ],
        icons: [
            'mdi-facebook',
            'mdi-twitter',
            'mdi-linkedin',
            'mdi-instagram',
        ],
        items: [
            { title: 'Contact Form',link:'#contactform', icon: 'mdi-ray-start-vertex-end' },
            { title: 'Team',link:'#team', icon: 'mdi-account' },
        ],
      title: 'New Notification',
      greeting: 'Hello',
      verified: '',
      body: '',
      type:'',
      status:'1',
      thanks:'Thanks for using our APP',
      url:'',
      html_body:'',
    }),

    computed: {
      bodyErrors () {
        const errors = []
        if (!this.$v.body.$dirty) return errors
        !this.$v.body.required && errors.push('Body is required')
        return errors
      },
      statusErrors () {
        const errors = []
        if (!this.$v.status.$dirty) return errors
        !this.$v.status.required && errors.push('User status is required')
        return errors
      },
      greetingErrors () {
        const errors = []
        if (!this.$v.greeting.$dirty) return errors
        !this.$v.greeting.maxLength && errors.push('greeting must be at most 45 characters long')
        !this.$v.greeting.required && errors.push('greeting is required.')
        return errors
      },
      verifiedErrors () {
        const errors = []
        if (!this.$v.verified.$dirty) return errors
        !this.$v.verified.required && errors.push('E-mail is required')
        return errors
      },
      typeErrors () {
        const errors = []
        if (!this.$v.type.$dirty) return errors
        !this.$v.type.required && errors.push('type number is required')
        return errors
      },
      titleErrors () {
        const errors = []
        if (!this.$v.title.$dirty) return errors
        !this.$v.title.maxLength && errors.push('title must be at most 45 characters long')
        !this.$v.title.required && errors.push('title is required.')
        return errors
      },
      thanksErrors () {
        const errors = []
        if (!this.$v.thanks.$dirty) return errors
        !this.$v.thanks.maxLength && errors.push('thanks must be at most 45 characters long')
        !this.$v.thanks.required && errors.push('thanks is required.')
        return errors
      },
    },


    methods: {

      submit () {
        this.$v.$touch()
        if (this.$refs.Form.validate()) {
            // submit form to server/API here...
            this.loading = true
            this.valid = false
             var form={
                "title":this.title,
                "greeting":this.greeting,
                "verified":this.verified,
                "body":this.body,
                "type":this.type,
                "status":this.status,
                "thanks":this.thanks,
                "url":this.url,
                "html_body":this.html_body,
            }
            this.sendData(form);
        }
      },
    sendData(data) {
        this.axios
        .post("send-notification-create", { data: { notification: data} })
        .then(() => {
            this.clear();
            this.allerros = [];
            this.sucess = true;
            this.loading = false
            this.valid = true
        })
        .catch(() => {
            this.loading = false
            this.valid = true
        });

    },
      clear () {
        this.$v.$reset()
        this.title= 'New Notification',
        this.greeting= 'Hello',
        this.verified= '',
        this.body= '',
        this.type='',
        this.status='1',
        this.thanks='Thanks for using our APP',
        this.url='',
        this.html_body=''
      },

    },
  }
</script>

